// Push card titles to bottom of the overlay
.section-explore-aspire .card .card-img-overlay{
  top: auto;
}


// Change stacking of Card Decks
.section-explore-aspire .card-deck {
  display: flex;
  flex-direction: column;

  @include media-breakpoint-up(md) {
    flex-flow: row wrap;
    margin-right: -$card-deck-margin;
    margin-left: -$card-deck-margin;
  }
}
